.Test {
    margin-left: 210px;
    margin-right: 20px;
    // padding: 20px;
    background-color: antiquewhite;

    .box {
        width: 100%;
        height: 40px;
        z-index: 5;
        
        // overflow: hidden;
        // border: 1px solid black;
        position: relative;
        // margin-bottom: 40px;
        .svgLines {
            //     // display: flex;
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
        }
    }
 
    .cell {
        border: 1px dashed gray;
        box-sizing: border-box;
        padding: 0px 10px;
        // width: 60;
        text-align: center;
        z-index: 90;
        height: 20px;
        margin: 10px 0;
        cursor: "move";
        overflow: hidden;
       
        p {
            font-size: 12px;
        background: rgb(112, 212, 112);

        }
    }

    .boardStyle {
        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
    }
}
